import Vue from 'vue'
import Router from 'vue-router'

const Game = ( (resolve) => {
  import('components/Game/Game').then( (module) => {
    resolve(module)
  })
})

const Developer = ( (resolve) => {
  import('components/Developer/Developer').then( (module) => {
    resolve(module)
  })
})


const EventDetail = ( (resolve) => {
  import('components/EventDetail/EventDetail').then( (module) => {
    resolve(module)
  })
})


const ReplyDetail = ( (resolve) => {
  import('components/ReplyDetail/ReplyDetail').then( (module) => {
    resolve(module)
  })
})

import Test from './Test5.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Game
    },
    {
      path: '/game',
      name: 'Game',
      component: Game
    },
    {
      path: '/developer',
      name: 'Developer',
      component: Developer
    },
    {
      path: '/eventDetail',
      name: 'EventDetail',
      component: EventDetail
    },
    {
      path: '/replyDetail',
      name: 'ReplyDetail',
      component: ReplyDetail
    }
  ]
})



