<template>
  <div class="demo">
    <div class="title">Basic Demo</div>
    

    <div class="scroll-list-wrap">
      <cube-scroll
        ref="scroll"
        :data="items"
        :scrollEvents="scrollEvents"
        :options="options"
        @scroll="onScrollHandle"
        @before-scroll-start="beforeScrollStart">

        <div v-for="item in items" class="list-item">{{ item }}</div>

        <div class="wrap" style="width: 300px;height: 80px;">
          <cube-scroll ref="scroll" :data="items2" direction="horizontal">
            <div class="list-wrapper">
              <div v-for="item in items2" class="list-item-two">{{ item }}</div>
            </div>
          </cube-scroll> 
        </div>

        
        <!-- <div style="width: 100%;height: 60px;border-bottom: 1px solid green;overflow-x: scroll;overflow-y: hidden;width: 100%; white-space: nowrap;">
          <div v-for="item in items2" class="list-item-two">{{ item }}</div>
        </div> -->

      </cube-scroll>
    </div>
  </div>
</template>

<script >
  import CubePage from './cube-page.vue'
  const _data = [
    ' start 😀 😁 😂 🤣 😃 😄 😀 😁 😂 🤣 😃 😄😀 😁 😂 ',
    '🙂 🤗 🤩 🤔 🤨 😐 ',
    '👆🏻 scroll up/down 👇🏻 ',
    '😔 😕 🙃 🤑 😲 ☹️ ',
    '🐣 🐣 🐣 🐣 🐣 🐣 ',
    '👆🏻 scroll up/down 👇🏻 ',
    '🐥 🐥 🐥 🐥 🐥 🐥 ',
    '🤓 🤓 🤓 🤓 🤓 🤓 ',
    '👆🏻 scroll up/down 👇🏻 ',
    '🦔 🦔 🦔 🦔 🦔 🦔 ',
    '🙈 🙈 🙈 🙈 🙈 🙈 ',
    '👆🏻 scroll up/down 👇🏻 ',
    '🚖 🚖 🚖 🚖 🚖 🚖 ',
    '✌🏻 ✌🏻 ✌🏻 ✌🏻 ✌🏻 ✌🏻 end'
  ]
  const _data2 = [
    '111',
    '2222 ',
    '333333️ ',
    '44444444 ',
    '5555555555 ',
    '666666666666 ',
    '77777777777777 ',
    '🙈 8888888888888 ',
    '999999999999999999 ',
  ]
export default {
  data() {
    return {
      items: _data,
      items2: _data2,
      itemIndex: _data.length,
      scrollbar: true,
      scrollbarFade: true,
      startY: 0,
      scrollToY: -200,
      scrollToTime: 700,
      scrollToEasing: 'bounce',
      scrollEvents: ['scroll', 'before-scroll-start', 'scroll-end'],
      options: {
        
      },
      scrollToEasingOptions: [
        {
          text: 'bounce',
          value: 'bounce'
        },
        {
          text: 'swipe',
          value: 'swipe'
        },
        {
          text: 'swipeBounce',
          value: 'swipeBounce'
        }
      ],
      customPullDown: false
    }
  },
  methods: {
    goToDeveloper() {
      this.$router.push({
        name: 'Developer'
      })
    },
    beforeScrollStart() {
      console.log("开始滚动啦")
    },
    onScrollHandle(pos){
      console.log("滚动啦")
    },
    resolveTitle (item) {
      return `${item.name}关注了问题 · ${item.postTime} 小时前`
    },
    resolveQuestionFollowers (item) {
      return `${item.answers} 赞同 · ${item.followers} 评论`
    }
  },

}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.scroll-list-wrap {
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  transform: rotate(0deg); // fix 子元素超出边框圆角部分不隐藏的问题
  overflow: hidden;
  height: 200px
  width: 100%
}
.list-item{
  width:100%;
  height: 50px;
  margin-bottom: 3px;
  border-bottom: 1px solid red;
}


.list-wrapper
  padding: 0 10px
  line-height: 60px
  white-space: nowrap
.cube-scroll-content
  display: inline-block
.list-item-two
  margin-right: 15px;
  background-color: red
  display: inline-block

</style>


